.catalog_home::before {
	content: "";
	position: absolute;
	width: 118px;
	height: 118px;
	transform: rotate(-90deg);
	opacity: .2;
	-webkit-filter: blur(20px);
	filter: blur(20px);
	background-image: linear-gradient(to left, #0ee881, #95ffcd 12%);
	border-radius: 50%;
	top: 100px;
	right: 24%;
}

.catalog_home::after {
	content: "";
	position: absolute;
	width: 114.8px;
	height: 114.8px;
	transform: rotate(-90deg);
	opacity: .5;
	-webkit-filter: blur(20px);
	filter: blur(20px);
	background-image: linear-gradient(to left, #4bcffa, #b5e9fd 0%);
	border-radius: 50%;
	bottom: 5%;
	left: 10%;
	-webkit-animation: random-animation-3 15s infinite;
	animation: random-animation-3 15s infinite;
}

.catalog_home .catalog_row:first-child {
	-webkit-animation: up-down 12s linear infinite alternate;
	animation: up-down 12s linear infinite alternate;
}

.catalog_home .catalog_row:nth-child(2) {
	-webkit-animation: down-up 12s linear infinite alternate;
	animation: down-up 12s linear infinite alternate;
}

.catalog_home .catalog_row:nth-child(3) {
	-webkit-animation: up-down-two 11.5s linear infinite alternate;
	animation: up-down-two 11s linear infinite alternate;
